<template>
  <div class="toolbar-button-wrapper"
       :class="{'selected':selected}"
    @click="onClicked">
    <div class="toolbar-button-icon text-center">
      <img :src="imgSrc">
    </div>
    <div class="toolbar-button-title text-center">
      {{ caption }}
      <div v-if="badge" class="badge badge-warning">{{ badge }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    // button: {
    //   type: Object,
    //   default: null
    // },
    badge: {
      type:  Number,
      default: 0
    },
    caption: {
      type:String,
      default: '(Unspecified)'
    },
    imgSrc: {
      type: String,
      default: ''
    },
    selected: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    onClicked () {
      const vm = this
      vm.$emit('click')
    }
  }
}
</script>

<style>
  .toolbar-button-wrapper.has-error {
    border-color: red;
  }
  .toolbar-button-wrapper.selected:hover {
    background-color: #7fa4cc;
  }
  .toolbar-button-wrapper.selected {
    background-color: #9fcdff;
  }
  .toolbar-button-wrapper:hover {
    background-color: rgba(127,127,127,.2);

  }
  .toolbar-button-wrapper {
    border: 2px solid transparent;
    border-bottom: none;
    display: inline-block;
    padding: 3px 18px 0px 18px;
    margin: 2px;
    margin-bottom: 0;
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
  }
  .toolbar-button-icon img {
    width: 64px;
    height: 64px;
    object-fit: contain;
  }
</style>